import { useDebounce, useDocumentTitle } from 'utils';
import { List } from './list';
import { SearchPanel } from './search-panel';
import styled from '@emotion/styled';
import { useProjects } from 'utils/project';
import { useUsers } from 'utils/user';
import { useProjectModal, useProjectsSearchParams } from './util';
import { ButtonNoPadding, ErrorBox, Row } from 'components/libs';
// import { Test } from 'components/test-closure';

// 基本类型，可以放到依赖里
// 组件状态，可以放在依赖里
// 非组件状态的对象，绝不可以放到依赖里
// const [keys] = useState<('name' | 'personId')[]>(['name', 'personId'])
// const [param, setParam] = useUrlQueryParam(['name', 'personId']);
// const projectsParam = {...param, personId: Number(param.personId) || undefined}
export const ProjectListScreen = () => {
  useDocumentTitle('项目列表', false);
  const { open } = useProjectModal();
  const [param, setParam] = useProjectsSearchParams();
  const { data, isLoading, error } = useProjects(useDebounce(param, 200));
  const { data: users } = useUsers();

  return (
    <Container>
      {/* <Test/> */}
      <Row between={true}>
        <h1>项目列表</h1>
        <ButtonNoPadding type="link" onClick={open}>
          创建项目
        </ButtonNoPadding>
      </Row>
      <SearchPanel param={param} setParam={setParam} users={users || []}></SearchPanel>
      <ErrorBox error={error} />
      <List dataSource={data || []} loading={isLoading} users={users || []}></List>
    </Container>
  );
};

ProjectListScreen.whyDidYouRender = false; // 单个组件追踪

const Container = styled.div`
  padding: 3.2rem;
  flex: 1;
`;
